import React from 'react'

function About() {
    return (
        <section id="banner">
        <header>
          <h2>About us</h2>
        </header>
        <p>
          Twins Garden is the combination of home stay and agrotourism in
          Rajakkad.
          <p>
            we offering home food and accommodation facilities and many more
            facilities.
            <p>We're excited to help you on your journey!</p>
          </p>
        </p>
        <footer>
          <a href="/contact" className="button style2 scrolly">
            Contact us
          </a>
        </footer>
      </section>
    )
}

export default About
